*,
*::before,
*::after {
  padding: 0;
  margin: 0;
  box-sizing: border-box; }

html {
  font-size: 11px; }

body {
  font-size: 1.6rem;
  font-family: 'Montserrat', sans-serif;
  color: #3E3F5D;
  background-color: #F5F4F9;
  &::-webkit-scrollbar {
    width: 1rem;
    background-color: white;

    &-track {
      background-color: white;
      box-shadow: inset 0 0 .6rem rgba(black, 0.3); }

    &-thumb {
      background-color: #615DFA; } } }

img {
  max-width: 100%;
  max-height: 100%; }

input, select, textarea {
  font-family: inherit; }

ul, ol {
  list-style: none; }

a {
  color: inherit; }

